import React, { useEffect } from 'react'
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";
import TextPlugin from "gsap/TextPlugin";
import { Flip } from 'gsap/Flip';



import bg_buildings from "../../img/TITLE_NEW/sean-pollock-PhYq704ffdA-unsplash 1.png"
import sky from "../../img/TITLE_NEW/sky_bg1.jpg"

import "./newTitlePage.css"
import Header from '../Header/Header';
import { useState } from 'react';
import { useLayoutEffect } from 'react';
import { useRef } from 'react';




export default function NewTitlePage() {
  gsap.registerPlugin(ScrollTrigger);
  gsap.registerPlugin(Flip); 
  let titleImageAnime = gsap.timeline();
  useEffect(()=>{
  titleImageAnime.to(
    ".sky",{
      scale:1,
      x:"-20vw",
      duration: 2
    })
    .from(
      ".buildings",{
        scale:1,
        y:0,
        opacity:1,
        duration:5
      }
    );
    ScrollTrigger.create({
      animation: titleImageAnime,
      trigger:".imgWrapper",
      start:"top 0%",
      end:"bottom 0%",
      scrub:3
    })
},[titleImageAnime])

const ref = useRef();
gsap.registerPlugin(TextPlugin);
// useLayoutEffect(() => {
//   gsap.fromTo(
//     ref.current,
//     {
//       text: {
//         value: "",
//         delimiter: "",
//       },
//       ease: "ease",
//     },
//     {
//       delay: 1,
//       duration: 5,
//       text: {
//         value: "Довіра,досвід, результат",
//         delimiter: "",
//       },
//       ease: "ease",
//     }
//   );
// }, []);
useLayoutEffect(() => {
  const state = Flip.getState(ref.current),
state1 = Flip.getState(".item1"),
state2 = Flip.getState(".item2");
  Flip.to(state, {duration: 7,transform:"scale(0)",opacity:0});
  Flip.to(state1,{duration: 7,transform:"translateX(calc(var(--index)*200))",opacity:0,delay:2})
  Flip.to(state2,{duration: 7,transform:"translateX(calc(var(--index)*-160))",opacity:0,delay:2})
},
[])

const [isOpen, setIsOpen] = useState(false);
  return (
    <>
    <section className='imgWrapper'>
    <div className="header">
        <div className="header_wrapper">
           <Header isOpen={isOpen} setIsOpen={setIsOpen}/>
        </div>
      </div>
    <img className='sky' src={sky} alt="sky" />
    <img className='buildings' src={bg_buildings} alt="scyscrapers" />
      <div className="textTitleWrapper">
      <div className='titleText' ref={ref}><span>Довіра,</span><span>досвід,</span><span>результат</span></div>
      <div className="textSubtitleWrapper">
      <div className='textSubtitle item1'>Ми допомагаємо приватним клієнтам, державним компаніям, малому і великому бізнесу вирішувати юридичні питання будь-якої складності. </div>
      <div className='textSubtitle item2'>Ми допомагаємо передбачати необхідні зміни і уникати таким чином складних ситуацій в майбутньому.</div>
      </div>
      </div>
    </section>
    </>
  )
}
